function getLayers() {
    // set metadata
    for (let layer of layersDefs) {
        layer.layer.meta = {title: layer.title};
    }

    // divide layers by groups
    const grouppedLayers = [];
    let group = {group: 'title', layers: layersDefs};
    grouppedLayers.push(group);

    return {
        layers: grouppedLayers,
    };
}

export {getLayers};
